import { Answer } from "./Answer";
export const checkFileTypeCSV = (fileName) => {
    // jsonファイル,pmjファイル以外は処理を止める
    if (fileName.match('.csv$') == null) {
        return true;
    }
    return false;
};
export const parseAnswer = (csv) => {
    var arr = csv.split('\n');
    return arr.map(row => swapAnswersFromCSV(row.split(',')));
};
const swapAnswersFromCSV = (str) => {
    let answer = new Answer(str[1], str.slice(2));
    return answer;
};
export const removeDoubleQuotes = (str) => {
    return str.replace(/[\"]/g, '');
};
